.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
    background-color: #a1b1b7;
    margin-top: 3em;
}

.dialogsItems a {
    padding: 10px;
    color: white;
}

.dialogsItems a:hover {
    color: yellow;
}

.dialogsItems a:active {
    color: red;
}

.dialogsItems .hover {
    color: white;
}

.messages {
    padding: 10px;
    color: white;
}

.messages .message {
    color: white;
}

